@import "./media.scss";

main
{

    a 
    {
        text-decoration: none;
    }
    a:hover
    {
        text-decoration: underline;
    }
    a:visited
    {
        //color: #1A0DAB;
        color :   #457bfb;
        
    }
    .Heading
    {
        margin-top: 2rem;
        text-align: center;

        .Heading-submissions
        {
            font-size: large;
            text-decoration: underline;
            .SpaceForSp
            {
                display: none;
            }
        }

        .Heading-title
        {
            font-size: xx-large;
            hyphens: none;
            overflow-wrap:break-word;

            .Nonbreak
            {
                display: inline-block;
            }
        }

        .Heading-author
        {
            margin: 1rem 0rem;
            font-size: large;
            display: flex;
            flex-direction: row;
            justify-content: space-evenly;
        }

        .Heading-affiliation
        {
            color: #666666;
        }

        @include pc
        {
            .Heading-images
            {
                margin: 1.0rem 0rem;

                .Heading-images-item
                {
                    height: 300px;
                    display: inline-block;
                }
                Img
                {
                    margin: 0 0.5rem;
                    height: 100%;
                }
            }
        }
            
        @include tab
        {

            .Heading-images
            {
                margin: 1.0rem 0rem;
            }
            Img
            {
                width: 80%;
            }
        }

        @include sp
        {
            .Heading
            {
                margin-top: 1rem;
            }
            .Heading-submissions
            {
                font-size: medium;
            }
            .SpaceForSp
            {
                display: inline;
            }

            .Heading-title
            {
                font-size: x-large;
                margin-top: 0.5rem;
            }

            .Heading-author
            {
                font-size: medium;
            }

            .Heading-images
            {
                margin: 1.0rem 0rem;
            }
            .Heading-images-item
            {
            }
            Img
            {
                width: 100%;
            }
        }
    }
    
    h2
    {
        color: #00a497;
        //color: #007f89;
        //color: #A05359;
        margin: 0.5rem 0rem;
    }
    ul
    {
        line-height: 2;
        margin-left: 2rem;
    }
    hr
    {
        height: 1px;
        margin-top: 1rem;
        background-color: #d3d3d3;
        //background-color: #f1e1e1;
        border: none;
    }
    div
    {
        hyphens: auto;
        line-height: 1.5;
    }

    .Abstract, .Acknowledgement
    {
        text-align: justify;
    }
    .Abstract
    {
        .Space
        {
            margin: 0.5rem 0rem;
        }
    }
    .SupplementalVideo 
    {
        .SupVideo
        {
            margin: 1rem 0rem;
            text-align: center;
        }
    }
    @include sp
    {
        .SupVideo {
            position: relative;
            width: 100%;
            padding-top: 56.25%;
            overflow:auto; 
            -webkit-overflow-scrolling:touch;
            //border:2px solid #ccc; 
          }
          .SupVideo iframe {
              position:absolute;
              top:0;
              left:0;
              width:100%;
              height:100%;
              border:none;
              display:block;
          }
    }
}

footer
{
    margin: 1rem 0rem;

    font-size: small;
    text-align: center;
    //background-color: #fff8ff;
    padding: 1rem 0rem 0rem 0rem;

    border-top: 1px solid #d3d3d3;
}
